ndow div.chatContainer div.chatTextOutput p.message{font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;font-size:1rem;margin:0.625rem 0}@media screen and (max-width: 500px){body section.chatRoom div.chatWindow div.chatContainer div.chatTextOutput p.message{font-size:1.4rem}}body section.chatRoom div.chatWindow div.chatContainer div.chatTextOutput p.message .sender{font-weight:bold}body section.chatRoom div.chatWindow div.chatContainer div.chatTextOutput p.notifyUserChange{text-align:center;font-style:italic}body section.chatRoom div.chatWindow div.chatInputContainer{height:10%;width:100%;display:flex;justify-content:space-between}body section.chatRoom div.chatWindow div.chatInputContainer textarea.messageInput{width:90%;height:100%}body section.chatRoom div.chatWindow div.chatInputContainer button.sendButton{width:10%;height:100%;background-color:#166088;color:#444241;cursor:pointer}body section.chatRoom div.chatWindow div.chatInputContainer button.sendButton:hover{background-color:#4F6D7A}body section.chatRoom div.membersPane{height:90%;width:21%;border:2px solid #4A6FA5;background-color:#DBE9EE;margin-right:2%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}@media screen and (max-width: 500px){body section.chatRoom div.membersPane{display:none}}body section.chatRoom div.membersPane h3.membersPaneTitle{margin:3%}body section.chatRoom div.membersPane div.membersContainer{display:flex;flex-direction:column;justify-content:flex-start;align-items:center;overflow-y:auto}body section.chatRoom div.membersPane div.membersContainer .member{margin:3%}#chat-container{display:flex;flex-direction:column;height:100vh;width:90vw;justify-content:space-evenly;border:2px solid black;padding:1%;margin:1%}

